import { invoke } from '@tauri-apps/api/core';
import { PlaylistRecord } from '@/database-types';

export async function getAllPlaylists(): Promise<PlaylistRecord[]> {
	try {
		const playlists = await invoke<PlaylistRecord[]>('get_all_playlists');
		return playlists;
	} catch (error) {
		console.error('Error fetching playlists:', error);
		throw error;
	}
}

export async function getPlaylistById(playlistId: string): Promise<PlaylistRecord> {
	try {
		const result = await invoke<{ playlist: any; audio_files: any[] }>('get_playlist_by_id', { id: playlistId });
		
		// Convertir el resultado al formato esperado
		return {
			...result.playlist,
			audioFiles: result.audio_files,
		} as PlaylistRecord;
	} catch (error) {
		console.error('Error fetching playlist:', error);
		throw error;
	}
}
